<template>
	<div class="setting-password">
		<div class="sms-title">验证码已发送至</div>
		<div class="sms-tel">
			<div class="sms-tel-num">18888888888</div>
			<div class="sms-get-code">(60秒)</div>
		</div>
		<div class="code-input"><input type="text" id="code_input" placeholder="输入短信验证码" maxlength="4"></div>
		<div class="password-input">
			<input type="text" id="password1" placeholder="请输入密码为6-16位字符，数字或者字母" maxlength="16">
		</div>
		<div class="password-input">
			<input type="text" id="password2" placeholder="请再次输入上方密码进行确认" maxlength="16">
		</div>
		<div class="register-btn" id="submit_btn" @click="submitFn">确认</div>
	</div>
</template>

<script>
export default {
	name: 'SettingPassword',
	data () {
		return {
			msg:"注册",
			clickBol:true,
			timer:"1",
		}
	},
	created (){
		console.log(this.$route.params.type)
		if (this.$route.params.type=="register") {
			document.title="注册";
		}else if (this.$route.params.type=="setting") {
			document.title="修改密码";
		}
		window.scrollTo(0, 0);
	},
	mounted () {
		console.log(this.$route.params.type)
		this.init();
	},
	beforeDestroy(){
		clearInterval(this.timer);
	},
	methods: {
		init: function () {
			var _this = this;
			_this.clickBol=true;
			var time=60;
			var $getCode = $(".sms-get-code");
			$getCode.text("("+time+"秒)").css({color: "#999"})
			_this.timer = setInterval(function(){
				if (time>0) {
					time--;
					$getCode.text("("+time+"秒)");
					console.log(_this.clickBol)
				}else{
					_this.clickBol=false;
					clearInterval(_this.timer);
					$getCode.text("获取验证码").css({color: "#007BF9"}).on('click', function() {
						if (_this.clickBol) return;
						console.log(_this.clickBol)
						_this.init();
					});;
				}
			},1000);
			$("input").on('input propertychange', function(e) {
				var isInput=0;
				$.each($("input"),function(i,o){
					if ($(o).val()!="") isInput++;
				});
				if (isInput==3) {
					$("#submit_btn").css({
						"background-color": '#000',
						"color": '#fff'
					})
				}else{
					$("#submit_btn").css({
						"background-color": '#BEBEBE',
						"color": '#999'
					})
				};
			});
		},
		submitFn:function(){
			if ($("#code_input").val()=="") {CF.showTips("验证码不能为空");return;}
			if ($("#password1").val()==""||$("#password1").val().length<6) {CF.showTips("密码长度需大于6位字符");return;}
			if ($("#password1").val()!=$("#password2").val()) {CF.showTips("密码不一致");return;}
			this.$router.push({ path: '/Login' })
		}
	}
}
</script>

<style scoped>
	input{
		width: 100%;
	}
	.setting-password{
		padding: 1rem;
	}
	.setting-password>div{
		margin: 3rem 0;
	}
	.setting-password .sms-title{
		margin: 2rem 0;
		font-size: 1.5rem;
	}
	.setting-password .sms-tel{
		margin: 2rem 0;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		align-items: center;
		-webkit-align-items: center;
	}
	.sms-get-code{
		color: #999;
	}
	.register-btn{
		background-color: #EBEBEB;
		color: #999;
		padding: 1rem;
		text-align: center;
	}
</style>



