<template>
	<div class="mine-coupon">
		<ul class="coupon-tab">
			<li class="cur">未使用</li>
			<li class="">已使用</li>
			<li class="">已过期</li>
		</ul>
		<ul class="coupon-list" id="unused_list">
			<li class="coupon-items">
				<div class="coupon-select"><img src="../assets/select1.png"></div>
				<div class="coupon-box">
					<div class="coupon-num">
						<div class="coupon-num-bg"><img src="../assets/coupon-bg.png"></div>
						<div class="coupon-value">
							<span class="coupon-value-unit">￥</span>
							<span class="coupon-value-price">30</span>
						</div>
						<div class="coupon-con">
							<div class="coupon-title">ONE T T恤15元代金券</div>
							<div class="coupon-date">2017.08.16-2017.10.16</div>
						</div>
					</div>
					<div class="coupon-isused">
						<div class="used-btn">立即使用</div>
					</div>
				</div>
			</li>
		</ul>
		<ul class="coupon-list" id="used_list">
			<li class="coupon-items">
				<div class="select-btn"></div>
				<div class="coupon-num"></div>
				<div class="coupon-con"></div>
				<div class="coupon-isused"></div>
			</li>
		</ul>
		<ul class="coupon-list" id="expired_list">
			<li class="coupon-items">
				<div class="select-btn"></div>
				<div class="coupon-num"></div>
				<div class="coupon-con"></div>
				<div class="coupon-isused"></div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'MineCoupon',
	data () {
		return {
			
		}
	},
	created (){
		document.title="优惠券";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.coupon-tab{
		display: flex;
		justify-content: space-around;
		display: -webkit-flex;
		-webkit-justify-content: space-around;
		color: #666;
		font-size: 0.875rem;
	}
	.coupon-tab li{
		padding: 0.5rem 0;
	}
	.cur{
		color: #000;
		border-bottom: 2px solid #000;
	}
	.coupon-list{
		padding: 0.5rem;
	}
	.coupon-items{
		padding: 0.5rem;
		display: flex;
		display: -webkit-flex;
		align-items: center;
	}
	.coupon-box{
		width: 100%;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.coupon-num{
		position: relative;
		display: flex;
	}
	.coupon-num-bg{
		width: 7rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.coupon-value{
		width: 7rem;
		height: 5.5rem;
		line-height: 5.5rem;
		text-align: center;
		position: relative;
		color: #fff;
		font-size: 1.25rem;
	}
	.coupon-value-price{
		font-size: 2.5rem;
		margin-left: -0.5rem;
	}
	.coupon-con{
		display: -webkit-flex;
		-webkit-flex-direction:column;
		-webkit-justify-content: space-between;
		display: flex;
		flex-direction:column;
		justify-content: space-between;
		font-size: 0.75rem;
		padding: 0.5rem;
	}
	.coupon-isused{
		display: flex;
		display: -webkit-flex;
		flex-direction:column-reverse;
		-webkit-flex-direction:column-reverse;
	}
	.coupon-select{
		width: 1rem;
		height: 1rem;
		margin-right: 0.5rem;
	}
	.used-btn{
		font-size: 0.75rem;
		border:1px solid #000;
		border-radius: 1rem;
		padding: 0.1rem 0.5rem;
		margin-bottom: 0.5rem;
	}
</style>



