<template>
	<div class="feed-back">
		<textarea name="" id=""></textarea>
		<div class="submit">提交</div>
	</div>
</template>

<script>
export default {
	name: 'FeedBack',
	data () {
		return {
			msg:"反馈"
		}
	},
	created (){
		document.title="反馈";
		window.scrollTo(0, 0);
	},
	mounted () {
		$("textarea").focus()
	},
	methods: {

	}
}
</script>

<style scoped>
	.feed-back{
		text-align: center;
	}
	textarea{
		width: 96%;
		height: 12rem;
		resize:none;
		padding: 1rem;
		box-sizing: border-box;
		background-color: #F3F3F3;
		box-sizing: border-box;
		margin: 0.5rem 0;
	}
	.submit{
		width: 40%;
		font-size: 1.125rem;
		text-align: center;
		margin: 1rem auto;
		background-color: #000;
		color: #fff;
		padding: 0.5rem 0;
		border-radius: 0.25rem;
	}
</style>



